import styled from '@emotion/styled'
import React,{Fragment, useEffect} from 'react'
import LinearProgress from '@material-ui/core/LinearProgress';
import "aos/dist/aos.css";
import AOS from 'aos';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import photoProfil from '../assets/img/rian_pp.jpg'
import bottomGapGrey from '../assets/img/bottom_gap_gray.svg'

function About() {

    const AboutContainer = styled.div`
        position:relative;
        margin-top:120px;
        .tag__container{
            display:flex;
            justify-content:center;
            .tag__about{
                display:inline-block;
                position:relative;
                &:after{
                    content: '';
                    display: block;
                    position: absolute;
                    bottom: -14px;
                    left: 10%;
                    width: 80%;
                    height: 3px;
                    background: #616161;
                }
            }
        }
        .about__body{
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 16px;
            padding:100px 5% 200px 5%;
            .about__info{
                text-align:center;
                .about__info__image-lazy{
                    width:200px;
                    height:200px;
                    object-fit: cover;
                    display:block;
                    margin: 0 auto;
                    border-radius:50%;
                }

                p{
                    margin-top:32px;
                    width: 70%;
                    text-align:center;
                    margin:32px auto;
                    color: #616161;
                }
            }
            .about__skill{
                .MuiLinearProgress-barColorPrimary{
                    background-color: #009CA1;
                }
                .about__skill__body{
                    margin-bottom: 12px;
                }
                .about__skill__info{
                    display:flex;
                    justify-content:space-between;
                    color: #616161;
                    margin-bottom:6px;
                }
            }
        }
        .bottom-gap-grey{
            position:absolute;
            bottom:-1px;
            left:0;
            width:100%;
        }
        
    `

    useEffect(()=>{
        AOS.init({
            duration : 600
        });
    },[])

    const skills = [
        {name: 'JavaScript (ES6)', number: 90},
        {name: 'HTML 5', number: 90},
        {name: 'CSS 3 ', number: 90},
        {name: 'SCSS', number: 90},
        {name: 'Bootstrap', number: 80},
        {name: 'React', number: 80},
        {name: 'PWA', number: 80},
        {name: 'Ionic', number: 80},
        {name: 'Git', number: 80},
        {name: 'Android Kotlin', number: 70},
    ]

    return (
        <AboutContainer>
            <div className="tag__container" id="about" data-aos="fade-up">
                <h2 className="tag__about">ABOUT</h2>
            </div>
            <div className="about__body">
                <div className="about__info" data-aos="fade-up">
                    <LazyLoadImage
                        className="about__info__image-lazy"
                        alt="photo profil"
                        effect="blur"
                        src={photoProfil} />
                    <p>I am a strong conceptual thinker who has a keen interest in all things related
to the technology, especially in the web and android. Enthusiasm and commitment to learn and develop
dynamic user interface and user friendly.</p>
                </div>
                <div className="about__skill" data-aos="fade-up">
                    {
                        skills.map((skill, index)=>(
                            <div key={index} className="about__skill__body">
                                <div className="about__skill__info">
                                    <p>{skill.name}</p>
                                    <p>{skill.number}</p>
                                </div>
                                <LinearProgress aria-label="skill-percentage" variant="determinate" value={skill.number} />
                            </div>
                        ))
                    }
                </div>
            </div>
            
            <img src={bottomGapGrey} className="bottom-gap-grey" alt="bottom gap grey" />
        </AboutContainer>
    )
}

export default About
